.slip-dragging {
    -webkit-transition-property: box-shadow, background-color;
    transition-property: box-shadow, background-color;
    -webkit-transition-duration: .18s;
            transition-duration: .18s;
    -webkit-transition-timing-function: ease;
            transition-timing-function: ease;
    /* background-color: gray !important;    */
    cursor: ns-resize;
}

.slip-shadow {
    box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2),
    0px 8px 10px 1px rgba(0, 0, 0, 0.14),
    0px 3px 14px 2px rgba(0, 0, 0, 0.12);
}

.slip-dropping {
    -webkit-transition-property:  background-color, box-shadow;
    transition-property:  background-color, box-shadow;
    /* transition-delay: 0.18s; */
    -webkit-transition-duration: .18s;
            transition-duration: .18s;
    -webkit-transition-timing-function: ease;
            transition-timing-function: ease;
}

.slip-transition {
    -webkit-transition: -webkit-transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: -webkit-transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1), -webkit-transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    will-change: transform;
}
body {
  touch-action: pan-y; /* allow vertical scroll but prevent swipe to go back in msEdge */
  background-color: tomato;
  font-family: Roboto;
  overflow-x: hidden;
}

/* not required - in SlipMove instead */
.list {
  position: relative; /* Required by react-flip-move - uncomment it to see console warning */
}

.list-item {
  position: relative; /* ensures that item is above all others in chrome */
  height: 50px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;
  border: 1px solid rgba(0,0,0,.125);
  margin-bottom: 2px;
  margin-top: 2px;
}

.slipmove-dragging {
  background-color: orange;
}

